html {
    --section-background-color: linear-gradient(to left, rgb(101 157 189), rgb(1 64 82));

    --imp-text-color: #DAAD86;
}

.light-brown {
    color: var(--imp-text-color) !important;
}

/* --------- */
/*  Preloader */
/* --------- */
#preloader {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 999998;
  background-color: #376681;
  background-repeat: no-repeat;
  background-position: center;
  animation: glower 1.5s step-start;
}

@keyframes glower {
    0% {
        background-image: url(assets/avatar.svg);
    }
    25% {
        background-image: url(assets/avatar-eyes.svg);
    }
    50% {
        background-image: url(assets/avatar.svg);
    }
    90% {
        background-image: url(assets/avatar-happy.svg);
    }
}

#preloader-none {
  opacity: 0;
}

#no-scroll {
  overflow: hidden;
  height: 100vh;
}

/* --------- */
/*Scrollbar   */
/* --------- */

::-webkit-scrollbar {
    width: 7px;
}

/* Track */
::-webkit-scrollbar-track {
    background: #bc986a;
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: rgb(218, 173, 134);
    border-radius: 12px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: rgb(218, 173, 134);
    border-radius: 12px;
}

/* --------- */
/* Navbar Section  */
/* --------- */
.sticky {
    background-color: #376681 !important;
    transition: all 0.3s ease-out 0s !important;
    box-shadow: 0px 10px 10px 0px rgba(9, 5, 29, 0.171) !important;
    backdrop-filter: blur(15px) !important;
}

.navbar {
    position: fixed !important;
    transition: all 0.3s ease-out 0s !important;
    padding: 0.3rem 2rem !important;
    font-size: 1.2rem !important;
}

.navbar-toggler {
    padding: 0.25rem 1.5rem !important;
    position: relative !important;
    background-color: transparent !important;
    border-color: transparent !important;
}

.navbar-toggler span {
    display: block !important;
    background-color: #FBECC1 !important;
    height: 4px !important;
    width: 27px !important;
    margin-top: 5px !important;
    margin-bottom: 5px !important;
    transform: rotate(0deg) !important;
    left: 0 !important;
    opacity: 1 !important;
}

.navbar-toggler:focus,
.navbar-toggler:active {
    outline: 0 !important;
}

.navbar-toggler span:nth-child(1),
.navbar-toggler span:nth-child(3) {
    transition: transform 0.35s ease-in-out !important;
    transition: transform 0.35s ease-in-out !important;
}

.navbar-toggler:not(.collapsed) span:nth-child(1) {
    position: absolute !important;
    left: 12px !important;
    top: 10px !important;
    transform: rotate(135deg) !important;
    opacity: 0.9 !important;
}

.navbar-toggler:not(.collapsed) span:nth-child(2) {
    height: 12px !important;
    visibility: hidden !important;
    background-color: transparent !important;
}

.navbar-toggler:not(.collapsed) span:nth-child(3) {
    position: absolute !important;
    left: 12px !important;
    top: 10px !important;
    transform: rotate(-135deg) !important;
    opacity: 0.9 !important;
}

@media (max-width: 767px) {
    .navbar {
        padding: 1rem 2rem !important;
        font-size: 1.4rem !important;
        background-color: #376681 !important;
    }

    .navbar-nav .nav-item a::after {
        display: none !important;
    }
}

.navbar-brand {
    color: rgb(250, 250, 250) !important;
}

.logo {
    height: 1.4em !important;
    width: 2.5em !important;
}

.logo:hover {
    animation: logo-animation 1.5s step-start;
}

@keyframes logo-animation {
    0% {
        transform: rotate(0);
    }
    10% {
        transform: rotate(10deg);
    }
    /* The following five values can be played with to make the jumping more or less extreme */
    20% {
        transform: rotate(-8deg);
    }
    30% {
        transform: rotate(14deg);
    }
    40% {
        transform: rotate(-4deg);
    }
    50% {
        transform: rotate(10deg);
    }
    60% {
        transform: rotate(0deg);
    }
    /* Reset for the last half to pause */
    100% {
        transform: rotate(0deg);
    }
}

.logo:hover {

}

.navbar-nav .nav-link {
    color: white !important;
    padding-right: 1rem !important;
    padding-left: 1rem !important;
}

.nav-link {
    padding: 0.8rem 1rem !important;
}

@media (max-width: 767px) {
    .nav-link {
        padding: 0.7rem 1rem !important;
    }
}

.navbar-nav .nav-item {
    position: relative;
    margin-left: 20px;
}

.navbar-nav .nav-item a {
    font-weight: 400;
    transition: all 0.3s ease-out 0s;
    position: relative;
    z-index: 1;
}

.navbar-nav .nav-item a::after {
    content: "";
    position: relative;
    display: block;
    height: 5px;
    width: 0;
    border-radius: 16px;
    background: #FBECC1;
    bottom: 1px;
    left: 0;
    z-index: -1;
    transition: all 0.3s ease-out 0s;
}

.navbar-nav .nav-item a:hover::after {
    width: 100%;
}

/* --------- */
/* Home section */
/* --------- */
.jump {
    animation-name: jump-animation; /* Refers to the name of your @keyframes element below */
    animation-duration: 2.1s; /* Change to speed up or slow down */
    animation-iteration-count: infinite; /* Never stop waving :) */
    display: inline-block;
}

@keyframes jump-animation {
    0% {
        transform: translateY(0);
    }
    10% {
        transform: translateY(14px);
    }
    /* The following five values can be played with to make the jumping more or less extreme */
    20% {
        transform: translateY(-8px);
    }
    30% {
        transform: translateY(14px);
    }
    40% {
        transform: translateY(-4px);
    }
    50% {
        transform: translateY(10px);
    }
    60% {
        transform: translateY(0px);
    }
    /* Reset for the last half to pause */
    100% {
        transform: translateY(0px);
    }
}

